import React, { useState, useEffect } from 'react';
import firebase from 'firebase/compat/app';
import 'firebase/compat/database';
import { Container, Row, Col, Spinner } from 'react-bootstrap';
import './Home.css'; // Import the custom CSS file
import ProductImage from '../../images/Product.png';

// Initialize Firebase
const firebaseConfig = {
  apiKey: "AIzaSyDOdD2JYw6G_Vhbqp_YG-KhtV5jQQyK02o",
  authDomain: "mall-9b358.firebaseapp.com",
  projectId: "mall-9b358",
  storageBucket: "mall-9b358.appspot.com",
  messagingSenderId: "986401248179",
  appId: "1:986401248179:web:ea8cdc142d12b6cbe8b1c6",
  measurementId: "G-5220XD5SYQ"
};

firebase.initializeApp(firebaseConfig);

const Home = () => {
  const [loading, setLoading] = useState(true);
  const [products, setProducts] = useState([]);
  const [expandedDescriptionIds, setExpandedDescriptionIds] = useState([]);

  const handleToggleDescription = (productId) => {
    if (expandedDescriptionIds.includes(productId)) {
      setExpandedDescriptionIds((prevIds) => prevIds.filter((id) => id !== productId));
    } else {
      setExpandedDescriptionIds((prevIds) => [...prevIds, productId]);
    }
  };

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const snapshot = await firebase.database().ref('products').once('value');
        const productData = snapshot.val()
        if (productData) {
          const productList = Object.entries(productData).map(([key, value]) => ({
            id: key,
            ...value
          }));
          setProducts(productList);
          setLoading(false);
        }
      } catch (error) {
        console.log('Error fetching products:', error);
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  return (
    <>
      <Container>
        <>
          {loading ? (
            <div className="loading-container">
              <Spinner animation="border" role="status" />
              <p className="loading-message">Fetching products...</p>
            </div>
          ) : (
            <Row>
              {products.map((product) => (
                <Col key={product.id} xs={12} sm={6} md={4} lg={3}>
                  <div className="product-card">
                    <div className="product-image">
                      <img src={ProductImage} alt={product.name} />
                    </div>
                    <div className="product-details">
                      <h3>{product.name}</h3>
                      <p>Price: {product.price}</p>
                      <p
                        className={`product-description ${expandedDescriptionIds.includes(product.id) ? 'expanded' : 'collapsed'
                          }`}
                      >
                        {product.description}
                      </p>
                      {product.description.length > 100 && (
                        <button
                          className="expand-button"
                          onClick={() => handleToggleDescription(product.id)}
                          type="button"
                        >
                          {expandedDescriptionIds.includes(product.id) ? 'Show less' : 'Show more'}
                        </button>
                      )}
                    </div>
                  </div>
                </Col>
              ))}
            </Row>
          )}
        </>
      </Container>
    </>
  );
};

export default Home;
