import React from 'react';
import './About.css';

const About = () => {
  return (
    <div className="about-container">
      <h1 className="about-heading">About Us</h1>
      <div className="about-content">
        {/* <img className="about-image" src="/images/company-logo.png" alt="Company Logo" /> */}
        <p className="about-description">
          Welcome to our online store! We are a leading e-commerce company committed to providing high-quality products
          and excellent customer service. With a wide range of products, we aim to fulfill all your shopping needs.
        </p>
        <p className="about-description">
          Our mission is to make online shopping convenient, enjoyable, and secure for our customers. We strive to
          deliver a seamless shopping experience with easy navigation, detailed product information, and fast shipping.
        </p>
        <p className="about-description">
          At our company, we value our customers and their satisfaction is our top priority. If you have any questions,
          concerns, or feedback, please don't hesitate to contact our customer support team. We are here to assist you
          and ensure your shopping experience is exceptional.
        </p>
      </div>
    </div>
  );
};

export default About;
