import React from 'react';
import './Contact.css';

const Contact = () => {
    const ownerContactDetails = {
        name: 'John Doe',
        email: 'johndoe@example.com',
        phone: '123-456-7890',
        address: '1234 Main Street, City, State, ZIP',
    };

    return (
        <div className="contact-container">
            
            <div className="contact-form mt-2">
                <h2 className="form-heading">Contact Us</h2>
                <form>
                    <div className="form-group">
                        <label htmlFor="name">Name</label>
                        <input type="text" id="name" name="name" placeholder="Enter your name" required />
                    </div>
                    <div className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="email" id="email" name="email" placeholder="Enter your email" required />
                    </div>
                    <div className="form-group">
                        <label htmlFor="message">Message</label>
                        <textarea id="message" name="message" rows="5" placeholder="Enter your message" required></textarea>
                    </div>
                    <button type="submit">Submit</button>
                </form>
            </div>
            <div className="contact-details">
                <h3 className="section-heading">Owner Contact Details</h3>
                <div className="owner-contact-info">
                    <p>
                        <span className="info-label">Name:</span> {ownerContactDetails.name}
                    </p>
                    <p>
                        <span className="info-label">Email:</span> {ownerContactDetails.email}
                    </p>
                    <p>
                        <span className="info-label">Phone:</span> {ownerContactDetails.phone}
                    </p>
                    <p>
                        <span className="info-label">Address:</span> {ownerContactDetails.address}
                    </p>
                </div>
            </div>
        </div>
    );
};

export default Contact;
